#skills .container {
  padding: 100px 0px;
  height: auto;
}
#skills h3{
  text-align: center;
}
.silder_container{
  width: 40%;
  margin-left: auto;
  margin-right: auto;
}
.heading {
  color: #ccaa35;
  padding-bottom: 20px;
}
.sub_title, .sub-heading {
  font-size: calc(1rem + 1vw);

  text-align: center;
  padding-bottom: 50px;
}
.row .col-6 {
  color: white;
  display: flex;
  justify-content: center;
}
.row .col-12 {
  padding: 40px;
}
.logo{
  margin-top: 10px;
}
.sub-heading {
  padding-bottom: 0;
  color: #ccaa35;
}
.container h3 span {
  color: #12bbe3;
}
.progress-bar {
  background-color: rgb(43, 41, 41);
}

.skills_content {
  height: 70px;
}

.logo img {
  width: 35px;
  border-radius: 10px;
  margin-right: 20px;
}
.skills_progress_bar {
  width: 100%;
}

/* skill set */
.skills_progress_bar label {
  color: rgb(216, 213, 213);
  font-size: 20px;

}
.mySwiper img{
  width:100%;
  margin-top: auto;

}
.mySwiper{
  margin-top:40px;
  width:35%;
  transition: all 2sec;
  display: flex;
  justify-content: center;
  align-items: center;
}
.skills_discription{
  font-size: calc(0.5rem + 1vw);
}
@media screen and (max-width: 576px){
 .skills_discription{
  font-size: calc(0.8rem + 1.5vw);
}

}
.progress{
  height: 10px;
  background-color: #ccb25e !important;
} 

.skills_progress_bar{
position: relative;
}
.skills_progress_bar span{
  position: absolute;
  top: 0;
  right: 0;
}
.skills_discription ul li{
  padding-bottom: 10px;
}
.skills_discription ul li::marker{
  color: #ccaa35;
}
