#projects {
  padding: 80px 0px;
}
.tab-content {
  color: white;
}
.project_content img {
  width: 100%;
  height: 260px;
}
.project_content .col-lg-4 {
  margin-bottom: 50px;
}
.modal {
  color: black;
}
p a {
  word-break: break-all;
}
.nav-link {
  color: white !important;
}
.nav-link.active {
  background-color: #ccaa35 !important;
  color: black !important;
  font-weight: 800;
  border: none;
}
ul.nav.nav-pills {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 30px;
}

/* model styles */
.model-footer > button {
  background-color: red;
}

#projects .card {
  /* margin: 30px; */
  padding: 40px 20px;
  background-color: rgb(49, 46, 46);
}
.project_content .row{
  background-color: rgb(38, 37, 37);
  border-radius: 10px;
}
.c_title {
  font-size: 25px;
  color: #ccaa35;
  padding: 0px 0px 10px;
  text-align: center;
}
@media screen and (max-width:767px) {
  h3.sub_heading{
    padding-top: 30px;
  }
}
#projects .heading {
  text-align: center;
  font-size: 50px;
}
.project_details{
  word-break:break-all;
  
}
.project_img img{
  width:100%;
}
.project_img button {
  margin-top: 30px;
  width:100%;
  text-align: center;
  text-decoration: none;
  padding: 8px 20px;
  font-weight: 900;
  border-radius: 10px;
  color: black;
  background-color: #ccaa35;
  border: none;
}
.project_details h4{
  font-size: 20px;
  color: #ccaa35;
}


.card h4 {
  font-size: 16px;
  text-decoration: underline;
}

.card img {
  width: 100%;
  height: 150px;
}
.project_details p{
  font-size: calc(0.8rem + 0.5vw);
}
.readmore button{
  margin-top: 10px;
}
p{
  text-align: justify;
  word-break: break-word;

}
.project_img button img{
  width:18px;
  height: 18px;
  margin-right:10px;
  margin-top: -3px;
}
.swiper-container {
  width: 100% !important;
}

.swiper-wrapper {
  width: 100% !important;
}
