.header {
  margin: 20px 0px;
  color: rgb(211, 209, 207);
  position: sticky;
  top: 0px;
  z-index: 9;
  background-color: rgb(0,0,0);
  padding: 15px 0px;
}
.header_logo{
  color: #ccaa35;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  position: relative;
}
.header_nav_links {
  align-items: baseline;

}
.header_nav_links .anchor{
  cursor: pointer;
  text-decoration: none;
  color: #ccaa35;
  padding: 5px 10px;
  margin: 0px 10px;
  font-size: 20px;
  border-radius: 5px;
}
.header_nav_links a:hover{
  border: 1px solid #ccaa35;
}
.header_nav_links a:focus{
  border-bottom: 5px solid #ccaa35;
}
.nav_icon .icons{
  color: #ccaa35;
  font-size: 35px
}
.nav_icon {
  position: absolute;
  top: -10px;
  right: 7px;
}
@media screen and (max-width: 767px) {
  .header .container {
    display: block;
    z-index: 99;
  }
  .header_nav_links {
    flex-direction: column;
    width: 100%;
    height: 70vh;
    position: absolute;
    top: 55px;
    left: -700px;
    transition: 0.3s;
    background-color: rgb(10, 9, 9, 0.9);
  }
  .header_nav_links a{
    width: 100%;
    text-align: center;
    padding: 20px 0px;
}
  .header_nav_links h5 {
    padding: 10px;
    background-color: #20afd1;
  }
  .header_nav_links.active{
    left: 0px;
  }
}

