#contactForm {
    padding: 100px 0px 50px;
    border-bottom: 2px solid gray;
  }
#contactForm .col-lg-6,#contactForm .col-12 {
    display: flex;
    flex-direction: column;
}
#contactForm .textarea{
    text-align: start;
    padding: 16px;
}
#contactForm input,#contactForm textarea{
    margin: 0px 20px 20px;
    padding: 10px 20px;
    border-radius: 5px;
    border: none;
}
#contactForm input:focus{
    border: none;
    background-color:rgb(181, 179, 179) ;
}
#contactForm label{
    margin-left: 20px;
    color: #ccaa35;
}
#contactForm .submitButtom{
    text-align: center;
    display: block;
}
#contactForm button{
    width: 250px;
    padding: 10px 30px;
    border-radius: 5px;
    background-color: #ccaa35;
    font-weight: 900;
    font-size: 20px;
}
.contact_links{
    background-color: rgb(49, 48, 48);
    border-radius: 20px;
    width: 80%;
}
.contact_links a{
    display: block;
    font-size: 17px;
    text-decoration: none;
    color: antiquewhite;
    cursor: pointer;
}
.contact_links a:hover {
    color: #ccaa35;
  }
.contact_links  img{
    width: 60px;
    padding-right: 10px;
  }
  .all_contact_details{
    flex-direction: column;
  }