#about_me {
  text-align: center;
  position: relative;
  padding: 100px 0px 50px;
}

.about_section .heading {
  font-size: 40px;
}
.about_section .discription {
  font-size: calc(0.5rem + 1vw);
}
#about_me .heading {
  color: #ccaa35;
  font-size: calc(1.5rem + 1.5vw);
}
@media screen and (max-width: 576px) {
  .about_section .discription {
    font-size: calc(0.8rem + 1.5vw);
  }
}
.per_img img {
  width: 100%;
  border-radius: 10px;
}
.btns button:hover{
  color: #ccaa35;
}
@media screen and (max-width: 400px) {
  .per_img img {
    width: 100%;
  }
}
