*{
  margin: 0px;
}
body{
  color: rgb(187, 185, 185);
  background-color: black;
  /* font-family: 'Libre Baskerville', serif; */
  font-family: "kalam", serif !important;
  box-sizing: border-box;
  overflow-x: hidden;

}
html{
  scroll-behavior: smooth;
}
h1,h2,h2.heading, h3{
  font-family: "Cinzel", serif !important;

}
h2,h2.heading{
  text-align: center;
  font-size: calc(1.2rem + 1.5vw) !important;
  color: #ccaa35;
  padding-bottom: 10px;
}
h3.sub_heading{
  font-family: "Cinzel", serif !important;
  color: #ccaa35;
}
.btns button a {
  font-size: 15px;
}