
#resume .container{
    padding:100px 0px 50px;

}
.col-12{
    padding: 50px;
}
.edu_institusion{
    width: 250px;
}
.edu_institusion, .job_discription{
    font-size: calc(0.9rem + 0.5vw);
}
.job_discription{
    text-align: justify;

}
@media screen and (max-width: 767px) {
    .job_discription{
        padding-left: 0px;
    }
}
.company_name, .course{
    padding: 10px 0px ;
    font-size: calc(1.2rem + 1vw);

}
.job_type{
    font-size: 20px;
    padding: 5px 0px 15px;
}
.company_name,.course{
    font-size: calc(1.325rem + .9vw);
}
.education_details .d-flex{
    font-size: 17px;
}
.experiance_details:not(:last-child){
    margin-bottom: 50px;
}
.experiance_details, .education_details{
    color: rgb(178, 178, 179);
    position: relative;
    padding-left: 30px;
}
.job_discription::before, .course::before{
    content: "";
    position: absolute;
    top: 17px;
    left: 0;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color:rgb(195, 193, 190);
}
.experiance_details:nth-child(1) .company_name::before{
    content: "";
    position: absolute;
    top: 100px;
    left: 0;
    width: 20px;
    height: 150px;
    /* border-radius: 50%; */
    background-color:rgb(159, 157, 155);
}
#resume .heading, #resume .title, .job_type{
    color: #ccaa35;
}
#resume .course, #resume .company_name{
    color:#ccaa35;
    font-size: calc(1.1rem + 0.5vw);
}
.education_details .d-flex{
    flex-direction: column;
}