

#hero_section {
  position: relative;
  width: 100%;
  height: auto;
  color: white;
  background: linear-gradient(to right,  rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url("../../images/bg/bg-4.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background: r;
}
.personal_details {
  width: 100%;
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#hero_section h1{
    color:#F6F1F1;
}
.work {
  font-family: "Cinzel", serif !important;

  font-size: calc(1.5rem + 1.5vw);
  color: gold;
  font-weight: 900;
}
h1.name{
  font-size: calc(1rem + 1.5vw) !important;
}
@media screen and (max-width: 400px) {
.work{
  font-size: calc(1.2rem + 1.2vw) ;
    
  }
  h1.name{
  font-size: 22px;

  }
}
.name {
  font-family: "Julee", cursive;
  font-weight: 900;
}
.btns button {
  width: 130px;
  padding: 8px;
  margin: 20px 5px 0px;
  border-radius: 10px;
  background-color: #ccaa35;
  border: 2px solid #ccaa35;
}

.btns {
  flex-wrap: wrap;
  text-align: center;
}
.btns button a {
  transition: all 0.3s;
  color: black;
  text-decoration: none;
  font-weight: 800;
}
/* .btns button:nth-child(1){
  margin-right: 20px
} */
.btns button:nth-child(2) a {
  color: #ccaa35;
}
.btns button:nth-child(2) {
  background-color: black;
  border: 2px solid #ccaa35;
}

.btns button:nth-child(1):hover {
  background: transparent;
  border: 2px solid #ccaa35;
}
.btns button:nth-child(1):hover a {
  color: #ccaa35;
}
.btns button:nth-child(2):hover {
  background-color: #ccaa35;
  border: 2px solid #ccaa35;
}
.btns button:nth-child(2):hover a {
  color: black;
}
